
import Header from './header';
import './App.css';

import Addtodo from './addtodo';
function App() {
  let initstate;
  if (localStorage.getItem("todos") === null) {
    initstate = [];
  }
  else {
    initstate = JSON.parse(localStorage.getItem("todos"));
  }
 
  return (
    <>
    <Header Title="To-do List" />
    <Addtodo  />
    </>
  );
}

export default App;
