import React from 'react'

export default function Header(props) {
    return (
      
        <nav className="navbar navbar-expand-lg navbar-light " style={{color:'white', backgroundColor:"black"}}>
  <div className="container-fluid">
    <a className="navbar-brand" href="/" style={{color:'white'}}>{props.Title}</a>
    <button style={{backgroundColor:'white'}} className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" style={{color:'white'}}></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-a active" aria-current="page" href="/" style={{color:'white'}}>Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-a" href="/about" style={{color:'white'}}>About</a>
        </li>
        <li className="nav-item dropdown" >
          <a className="nav-a dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false" style={{color:'white'}}>
            Dropdown
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown" >
            <li><a className="dropdown-item" href="/carousel" >Carousel</a></li>
            <li><a className="dropdown-item" href="/aboutr" >AboutR</a></li>
            <li><hr className="dropdown-divider" /></li>
            <li><a className="dropdown-item" href="#" >Something else here</a></li>
          </ul>
        </li>
        <li className="nav-item">
          <a className="nav-a" href="/form"  aria-disabled="true" style={{color:'white'}}>Form</a>
        </li>
      </ul>
      <form className="d-flex">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
        <button className="btn btn-outline-success" type="submit" style={{color:'white'}}>Search</button>
      </form>
    </div>
  </div>
</nav>

    )
  
}
Header.defaultProps={
    Title:"Your Title Here"
}
